import React from 'react';
import { useState } from 'react';

import { LineChart, Line, XAxis, YAxis, Legend, CartesianGrid, Tooltip } from 'recharts';

const Chart = ({ data }) => {

  {/* Funktion fr att toggla mellan vilka linjer som ska visas p grafen.
  Vilka linjer som visas beror p vilka knappar som r intryckta/toggleade */}
  function toggleState(type) {
    switch (type) {
      case "cement":
        if (cement) {
          setCement(false);
        } else {
          setCement(true);
        }
        break;

      case "gasfuels":
        if (gasfuels) {
          setGasfuels(false);
        } else {
          setGasfuels(true);
        }
        break;

      case "gasflaring":
        if (gasflaring) {
          setGasflaring(false);
        } else {
          setGasflaring(true);
        }
        break;

      case "liquidfuels":
        if (liquidfuels) {
          setLiquidfuels(false);
        } else {
          setLiquidfuels(true);
        }
        break;

      case "solidfuels":
        if (solidfuels) {
          setSolidfuels(false);
        } else {
          setSolidfuels(true);
        }
        break;

      case "total":
        if (total) {
          setTotal(false);
        } else {
          setTotal(true);
        }
        break;
    } 
  }

  {/* States som bestmmer vilka linjer som ska visas i grafen */}
  const [cement, setCement] = useState(false);
  const [gasfuels, setGasfuels] = useState(false);
  const [gasflaring, setGasflaring] = useState(false);
  const [liquidfuels, setLiquidfuels] = useState(false);
  const [solidfuels, setSolidfuels] = useState(false);
  const [total, setTotal] = useState(true);

    // Funktion som dper om ett namn i ett objekt, i detta fall frn engelska till svenska
    const renameProperty = (obj, oldName, newName) => {
    if (obj.hasOwnProperty(oldName)) {
        obj[newName] = obj[oldName];
        delete obj[oldName];
    }
    };

    // Loopa igenom varje objekt i arrayn och dp om namnen till svenska, t.ex. "Gas Fuel" to "Gasbrnslen"
    data.forEach(item => {
        renameProperty(item, "Gas Fuel", "Gasbrnslen");
        renameProperty(item, "Gas Flaring", "Gasfackling");
        renameProperty(item, "Liquid Fuel", "Flytande brnslen");
        renameProperty(item, "Solid Fuel", "Solida brnslen");
    });

  return (
    <>
    <div className="chartContainer">
        {/* Skriv ut en graf med Recharts */}
        <LineChart width={768} height={400} data={data} margin={{ top: 5, right: 20, left: 10, bottom: 5 }}>
        
        {/* Stt X-axis till rtal */}
        <XAxis dataKey="Year" />
        <Tooltip />
        <Legend />
        <CartesianGrid stroke="#f5f5f5" />

        {/* Visa diverse linjer beroende p om deras state r satta till true eller false */}
        {cement ? 
        <Line type="monotone" dataKey="Cement" stroke="#2980b9" yAxisId={0} />
        :
        null
        }
        {gasfuels ? 
        <Line type="monotone" dataKey="Gasbrnslen" stroke="#95a5a6" yAxisId={1} />
        :
        null
        }
        {gasflaring ? 
        <Line type="monotone" dataKey="Gasfackling" stroke="#9b59b6" yAxisId={2} />
        :
        null
        }
        {liquidfuels ? 
        <Line type="monotone" dataKey="Flytande brnslen" stroke="#e67e22" yAxisId={3} />
        :
        null
        }
        {solidfuels ? 
        <Line type="monotone" dataKey="Solida brnslen" stroke="#2ecc71" yAxisId={4} />
        :
        null
        }
        {total ? 
        <Line type="monotone" dataKey="Total" stroke="#3498db" yAxisId={5} />
        :
        null
        }
        
        </LineChart>
        
    </div>

    {/* Knappar fr vilka linjer/processer/mnen som ska visas */}
    <div className="checkBoxesContainer">
    <label>
    <input className="buttonCheckbox" type="checkbox" onClick={() => toggleState("cement")}></input>
    <span className="button-label">Cement</span>
    </label>
    <label>
    <input className="buttonCheckbox" type="checkbox" onClick={() => toggleState("gasfuels")}></input>
    <span className="button-label">Gasbrnslen</span>
    </label>
    <label>
    <input className="buttonCheckbox" type="checkbox" onClick={() => toggleState("gasflaring")}></input>
    <span className="button-label">Gasfackling</span>
    </label>
    <label>
    <input className="buttonCheckbox" type="checkbox" onClick={() => toggleState("liquidfuels")}></input>
    <span className="button-label">Flytande brnslen</span>
    </label>
    <label>
    <input className="buttonCheckbox" type="checkbox" onClick={() => toggleState("solidfuels")}></input>
    <span className="button-label">Solida brnslen</span>
    </label>
    <label>
    <input className="buttonCheckbox" type="checkbox" onClick={() => toggleState("total")} defaultChecked></input>
    <span className="button-label">Totalt</span>
    </label>
    </div>
    </>
  );
};

export default Chart;