import React from 'react';

import { LineChart, Line, XAxis, CartesianGrid, Tooltip } from 'recharts';

/* Graf fr att visa havsniver genom historien */
const Chart4 = ({ data }) => {

  const modifiedData = data.map(entry => {
    // Extrahera endast ret ur "Time" istllet fr ven mnad och dag
    const year = entry["Time"].split("-")[0];
    
    // Skapa ett nytt objekt med det modifierade vrdet fr Time
    return {
      "Global medelhavsniv": entry["GMSL"],
      "GMSL uncertainty": entry["GMSL uncertainty"],
      "Time": year
    };
  });

  return (
    <div className="chartContainer">
        <LineChart width={768} height={400} data={modifiedData} margin={{ top: 5, right: 20, left: 10, bottom: 5 }}>
        <XAxis dataKey="Time" />
        <Tooltip />
        <CartesianGrid stroke="#f5f5f5" />
        <Line type="monotone" dataKey="Global medelhavsniv" stroke="#ff7300" yAxisId={0} />
        </LineChart>
        
    </div>
  );
};

export default Chart4;